import React, { createContext, useReducer } from 'react';
import { HashRouter as Router } from "react-router-dom";
import './css/style.css';
import Container from './components/container'
import Header from './components/header'



function App(){


  return(
        <Router>
        <div className="App">
        <Header />
        <Container />
        </div>          
        </Router>
  ) 
};

export default App