import React, { useState, useEffect } from 'react';
import '../css/style.css';
import mvp from '../assets/Scoreboard_mvp.webp'
import { Button, Card, CardDeck, Container, Col, Row } from 'react-bootstrap';
import { LabelList, BarChart, Cell, RadarChart, PieChart, Pie, ResponsiveContainer, Label, Treemap, RadialBarChart, RadialBar, PolarGrid, PolarAngleAxis, PolarRadiusAxis, Radar, Bar, YAxis, Legend, Text, LineChart, XAxis, Tooltip, CartesianGrid, Line } from 'recharts'
const config = require('../config.json');
const kentoData = require('../data/rankme-24-07-20.json')

  // add new kv pairs to json
  Object.keys(kentoData).map(
    function(object){
      kentoData[object]["accuracy"]= +kentoData[object].hits / +kentoData[object].shots * 100
  });

  Object.keys(kentoData).map(
    function(object){
      kentoData[object]["kd"]= +kentoData[object].kills / +kentoData[object].deaths
  });

  Object.keys(kentoData).map(
    function(object){
      kentoData[object]["kda"]= (+kentoData[object].kills +  +kentoData[object].assists) / +kentoData[object].deaths
  });

const killData = [
  ...kentoData
]

const shotData = [
  ...kentoData
]

const heatmapData = [
  ...kentoData
]

const accuracyData = [
  ...kentoData
]

const mvpData = [
  ...kentoData
]

const kdData = [
  ...kentoData
]

const kdaData = [
  ...kentoData
]

const weaponData = [
  ...kentoData
]

var sumKnife = weaponData.map(weapon => weapon.knife).reduce((a, b) => +a + +b);
var sumGlock = weaponData.map(weapon => weapon.glock).reduce((a, b) => +a + +b);
var sumUspSilencer = weaponData.map(weapon => weapon.usp_silencer).reduce((a, b) => +a + +b);
var sumP250 = weaponData.map(weapon => weapon.p250).reduce((a, b) => +a + +b);
var sumDeagle = weaponData.map(weapon => weapon.deagle).reduce((a, b) => +a + +b);
var sumElite = weaponData.map(weapon => weapon.elite).reduce((a, b) => +a + +b);
var sumHkp2000 = weaponData.map(weapon => weapon.hkp2000).reduce((a, b) => +a + +b);
var sumFiveSeven = weaponData.map(weapon => weapon.fiveseven).reduce((a, b) => +a + +b);
