import React, { useState, useContext } from 'react';
import { NavLink, Link, useHistory } from "react-router-dom";
import Nav from 'react-bootstrap/Nav'
import { Navbar, NavDropdown } from 'react-bootstrap/'
import logo from '../assets/cs-logo-inv.png'

var config = require('../config.json')

var Header = () => {

  return (
    <>

    <Navbar collapseOnSelect defaultExpanded expand="sm">
    <Navbar.Brand> <img src={logo} alt="logo" width="40" height="40" className="d-inline-block align-top"/></Navbar.Brand>

    <Navbar.Toggle aria-controls="responsive-navbar-nav" />
    <Navbar.Collapse id="responsive-navbar-nav">
      <Nav className="mr-auto">
        <NavLink to="/start" activeClassName="selected">dash-go</NavLink>
      </Nav>
      <Nav>       

      </Nav>
    </Navbar.Collapse>
  </Navbar>
  <br></br>
  </>

  );
}

export default Header;